<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">班牌首页轮播图设置</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="more">更多</span>
        </div>
        <div class="f28 c333" style="padding:0.84rem 0 1.7rem;">
            <div class="tap f28">
                <button v-bind:class="active1 ? 'bg_6b7':'b7f'" @click="yes">已上架</button>
                <button v-bind:class="!active1 ? 'bg_6b7':'b7f'" @click="no">未上架</button>
            </div>
            <div class="pl-30 pr-30" v-if="active1">
                <van-tabs v-model="active" color='#6B77FF' @click="onClick">
                    <van-tab title="班级风采">
                        <ul class="content">
                            <li class="b_line pr-30" v-for="(item,index) in list" :key="index" @click="$router.push({path:'/honorDetail',query:{type:2,id:item.id}})">
                                <img :src="$api.fileUrl + item.image">
                                <span>{{item.title}}</span>
                            </li>
                        </ul>
                        
                    </van-tab>
                    <van-tab title="班级荣誉">
                        <ul class="content">
                            <li class="b_line pr-30" v-for="(item,index) in list1" :key="index" @click="$router.push({path:'/honorDetail',query:{type:1,id:item.id}})">
                                <img :src="$api.fileUrl + item.image">
                                <span>{{item.title}}</span>
                            </li>
                        </ul>
                    </van-tab>
                    <van-tab title="班级简介">
                        <ul class="content">
                            <li class="b_line pr-30" v-for="(item,index) in list2" :key="index" @click="$router.push({path:'/honorDetail',query:{type:$route.query.type,id:item.id}})">
                                <img :src="$api.fileUrl + item.image">
                                <span>{{item.title}}</span>
                            </li>
                        </ul>
                    </van-tab>
                </van-tabs>
            </div>
            <div v-else class="pl-30 pr-30">
                <van-tabs v-model="activeDown" color='#6B77FF' @click="onClick">
                    <van-tab title="班级风采">
                        <ul class="content">
                            <li class="b_line pr-30" v-for="(item,index) in list3" :key="index" @click="$router.push({path:'/honorDetail',query:{type:2,id:item.id}})">
                                <img :src="$api.fileUrl + item.image">
                                <span>{{item.title}}</span>
                            </li>
                        </ul>
                    </van-tab>
                    <van-tab title="班级荣誉">
                        <ul class="content">
                            <li class="b_line pr-30" v-for="(item,index) in list4" :key="index" @click="$router.push({path:'/honorDetail',query:{type:1,id:item.id}})">
                                <img :src="$api.fileUrl + item.image">
                                <span>{{item.title}}</span>
                            </li>
                        </ul>
                    </van-tab>
                    <van-tab title="班级简介">
                        <ul class="content">
                            <li class="b_line pr-30" v-for="(item,index) in list5" :key="index" @click="$router.push({path:'/honorDetail',query:{type:$route.query.type,id:item.id}})">
                                <img :src="$api.fileUrl + item.image">
                                <span>{{item.title}}</span>
                            </li>
                        </ul>
                    </van-tab>
                </van-tabs>
            </div>
            <van-popup v-model="show" class="bannerSet" position="bottom" :overlay="true">
                <div>
                    <p class="b_line" @click="routTurn(1)">发 布</p>
                    <p class="b_line" @click="routTurn(2)">下 架</p>
                    <p @click="routTurn(3)">删 除</p>
                </div>
                <p @click="show=false">取 消</p>
            </van-popup>
        </div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            active1: true,
            active:0,
            activeDown:0,
            show:false,
            show1:false,
            list:[],//已上架班级风采
            list1:[],//已上架班级荣誉
            list2:[],//已上架班级简介
            list3:[],//未上架班级风采
            list4:[],//未上架班级荣誉
            list5:[],//未上架班级简介
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/class'});//返回上一层
        },
        yes(){
            this.active1=true;
            this.getBannerByTypeAndState();
        },
        no(){
            this.active1=false;
            this.getBannerByTypeAndState();
        },
        returnClose(){
            this.show1 = false;
        },
        lookDetail(index){
            var list=this.list;
            list[index].isSee=true;
            this.list=list;
            this.$router.push({path:'/leaveDetail',query:{active:this.active2}})
        },
        more(){
            // this.show1 = true;
            this.show=!this.show;
            // this.$router.push({path:'/editclasscards'})
        },
        routTurn(num){
            this.show=!this.show;
            this.$router.push({path:'/editclasscards',query:{type:num}})
        },
        onClick(){
            this.getBannerByTypeAndState();
        },
        getBannerByTypeAndState(){//type:1已上架2未上架 state : 1班级简介2班级荣誉3班级风采
            this.$toast.loading({
                mask: true,
                duration:0
            });
            var param = {
                classesId : JSON.parse(localStorage.getItem('zhxy_user')).classesId
            };
            if(this.active1 && this.active == 0){
                param.type = 1;
                param.state = 3;
            }else if(this.active1 && this.active == 1){
                param.type = 1;
                param.state = 2;
            }else if(this.active1 && this.active == 2){
                param.type = 1;
                param.state = 1;
            }else if(!this.active1 && this.activeDown == 0){
                param.type = 2;
                param.state = 3;
            }else if(!this.active1 && this.activeDown == 1){
                param.type = 2;
                param.state = 2;
            }else if(!this.active1 && this.activeDown == 2){
                param.type = 2;
                param.state = 1;
            }
            this.$http({url:this.$api.class.getBannerByTypeAndState},param).then(data => {
                console.log(data);  
                if(data.code == 0){
                    if(this.active1 && this.active == 0){
                        this.list = data.data;
                    }else if(this.active1 && this.active == 1){
                        this.list1 = data.data;
                    }else if(this.active1 && this.active == 2){
                        this.list2 = data.data;
                    }else if(!this.active1 && this.activeDown == 0){
                        this.list3 = data.data;
                    }else if(!this.active1 && this.activeDown == 1){
                        this.list4 = data.data;
                    }else if(!this.active1 && this.activeDown == 2){
                        this.list5 = data.data;
                    }
                    this.$toast.clear();
                }else{
                    this.$toast(data.msg);
                }
            },err => {
                this.$toast('出错了');
            })
        }
    },
    created() {
        this.getBannerByTypeAndState(1,3);
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    .tap{
        display: flex;
        justify-content: space-between;
        margin:0.3rem 0.3rem 0.2rem;
        height:.6rem;
        border-radius: 0.1rem;
        overflow: hidden;
        border:0.02rem solid #6B77FF;
        color:#fff;
    }
    .tap button{
        width:50%;
        border:0;
        background:#fff;
    }
    .content{position: absolute;top: 0.8rem;left: 0;right: 0;height: calc(100vh - 2.8rem);overflow: auto;}
    .content img{
        width:2.4rem;
        height:1.34rem;
        margin-right: .3rem;
        border-radius: .1rem;
        border:1px solid #EBEBEB;
    }
    li{
        padding:0.3rem 0.3rem 0.2rem 0;
    }
    li div{
        display: flex;
        justify-content: space-between;
        align-items: center
    }
    li img{
        width:0.14rem;
        height:0.14rem;
    }
    .van-tabs__wrap{
        border-width:0!important;
    }
    .van-popup{
        background: rgba(255, 255, 255, 0);
        color: #333;
        font-size: .32rem;
    }
    .van-popup p{
        
       
        line-height: .93rem;
        text-align: center;
    }
    .van-popup>p{
        border-radius: .1rem;
        background: #fff;
        margin:.13rem .3rem .3rem;
    }
    .van-popup>div{
        border-radius: .1rem;
        background: #fff;
        margin:0 .3rem;
    }
</style>




